<script>
export default {
  name: 'ModalContact',
  props: {
    modelValue: {
      type: Boolean,
      required: true
    }
  },
  emits: ['update:modelValue'],
  data() {
    return {
      isVisible: this.modelValue,
      form: {
        name: '',
        email: '',
        message: ''
      }
    }
  },
  watch: {
    modelValue(newVal) {
      this.isVisible = newVal;
      if (newVal) {
        document.body.style.overflow = 'hidden';
      } else {
        document.body.style.overflow = '';
      }
    }
  },
  mounted() {
    if (this.modelValue) {
      document.body.style.overflow = 'hidden';
    }
  },
  unmounted() {
    document.body.style.overflow = '';
  },
  methods: {
    submitForm() {
      alert('Спасибо за внимание :)');
      this.form = {
        name: '',
        email: '',
        message: ''
      };
      this.$emit('update:modelValue', false);
    }
  }
}
</script>


<style scoped>
.modal-enter-active,
.modal-leave-active {
  transition: opacity 0.3s ease;
}

.modal-enter-from,
.modal-leave-to {
  opacity: 0;
}

.modal-fade-enter-active,
.modal-fade-leave-active {
  transition: opacity 0.2s ease;
}

.modal-fade-enter-from,
.modal-fade-leave-to {
  opacity: 0;
}
</style>